#customers #web {
  display: none;
  height: 95vh;
  width: 70vw;
}

#customers #web-all {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

button{
  background-color: transparent;
  border-color: transparent;
}

#toggle{
  height: 5vh;
  width: 10vw;
}

#customers #mobile {
  display: flex;
  height: 95vh;
}

#footer {
  margin-top: 8vh;
  height: 20vh;
  width: 100vw;
}

#background {
  width: 100vm;
  height: 100vh;
  position:absolute;
}

#customers{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  position: relative;
}

@media screen and (min-width: 500px){
  #customers #mobile{
    display: none;
  }

  #customers #web{
    display: flex;
  }
}
